@import "../../scss-base/mixin_media";

@include desktop() {
  .main-component{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 70rem;
    color: #eeeeee;

    background-position: center; /* 이미지를 중앙에 배치 */
    background-size: cover; /* 이미지가 컨테이너를 채우도록 조절 */
    background-repeat: no-repeat; /* 이미지 반복 방지 */

    > * {
      position: relative;
      z-index: 2;
    }

    .hello-world{
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 10rem;
      text-shadow: 2px 2px 10px;
      padding-bottom: 3rem;

      &.fade-in {
        animation: puffIn 1s;
      }
      &.fade-out{
        animation: puffOut 1s;
      }

    }

    .hello-world-korean{
      display: flex;
      flex-direction: column;
      padding-top: 2rem;
      justify-content: space-between;
      font-weight: bolder;

      &.fade-in {
        animation: puffIn 1s;
      }
      &.fade-out{
        animation: puffOut 1s;
      }

      .korean-text-1{
        font-size: 3rem;
        padding-bottom: 2rem;
      }
      .korean-text-2{
        font-size: 3rem;
      }
    }

    // Loading 디자인
    .loading-text{
      font-size : 6rem;
      margin-bottom: 10rem;
    }
    .loading-circles{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 30rem;

      font-size: 5rem;
      .odd-circle{
        animation: odd-circle-move infinite 3s ease-in-out forwards;
      }
      .even-circle{
        animation: even-circle-move infinite 3s ease-in-out forwards;
      }
    }
  }
}

@include mobile() {
  .main-component{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40rem;
    color: #eeeeee;


    background-position: center; /* 이미지를 중앙에 배치 */
    background-size: cover; /* 이미지가 컨테이너를 채우도록 조절 */
    background-repeat: no-repeat; /* 이미지 반복 방지 */

    /* 나머지 컨텐츠는 오버레이 위에 표시되도록 z-index 설정 */
    > * {
      position: relative;
      z-index: 2;
    }

    .hello-world{
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 4rem;
      text-shadow: 2px 2px 10px;
      padding-bottom: 3rem;

      &.fade-in {
        animation: puffIn 1s;
      }
      &.fade-out{
        animation: puffOut 1s;
      }

    }

    .hello-world-korean{
      display: flex;
      flex-direction: column;
      padding-top: 2rem;
      justify-content: space-between;
      font-weight: bolder;

      &.fade-in {
        animation: puffIn 1s;
      }
      &.fade-out{
        animation: puffOut 1s;
      }

      .korean-text-1{
        display: flex;
        justify-content: center;
        align-items: center;
        padding : 10px;
        margin-bottom: 10px;

        font-size: 30px;
      }
      .korean-text-2{
        display: flex;
        justify-content: center;
        align-items: center;
        padding : 10px;
        margin-bottom: 10px;

        font-size: 25px;
      }
    }

    // Loading 디자인
    .loading-text{
      font-size : 50px;
      margin-bottom: 150px;
    }
    .loading-circles{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 300px;

      font-size: 60px;
      .odd-circle{
        animation: odd-circle-move infinite 3s ease-in-out forwards;
      }
      .even-circle{
        animation: even-circle-move infinite 3s ease-in-out forwards;
      }
    }
  }
}


@keyframes odd-circle-move {
  0%{
    opacity: 0;
    transform: translateY(-5rem);
  }
  50%{
    opacity: 100%;
    transform: translateY(0rem);
  }
  100%{
    opacity: 0;
    transform: translateY(-5rem);
  }
}

@keyframes even-circle-move {
  0%{
    opacity: 100%;
    transform : translateY(0rem);
  }
  50%{
    opacity: 0;
    transform: translateY(-5rem);
  }
  100%{
    opacity: 100%;
    transform : translateY(0rem);
  }
}

@keyframes puffIn {
  0% {
    opacity: 0;
    transform-origin: 50% 50%;
    transform: scale(2, 2);
    filter: blur(2px);
  }
  100% {
    opacity: 1;
    transform-origin: 50% 50%;
    transform: scale(1, 1);
    filter: blur(0px);
  }
}
@keyframes puffOut {
  0% {
    opacity: 1;
    transform-origin: 50% 50%;
    transform: scale(1, 1);
    filter: blur(0px);
  }
  100% {
    opacity: 0;
    transform-origin: 50% 50%;
    transform: scale(2, 2);
    filter: blur(2px);
  }
}



@keyframes spaceInUp {
  0% {
    opacity: 0;
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
    -webkit-transform: scale(0.2) translate(0%, -200%);
    transform: scale(0.2) translate(0%, -200%);
  }

  100% {
    opacity: 1;
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
    -webkit-transform: scale(1) translate(0%, 0%);
    transform: scale(1) translate(0%, 0%);
  }
}